.header{
  height: 50px;
  width: 100%;
  background-color: black;
  color: white;
  text-align: center;
  padding-top: 20px;
  font-size: 30px;
  font-weight: bold;
}
.space{
  display: flex;
  padding: 20px;
  background-color: rgb(43, 42, 42);
  color: white;
}
.code-area,.output-area{
  flex-basis: 50%;
  padding: 10px;
  display: flex;
  flex-direction: column;

}

textarea{
  margin-top: 20px;
  height: 500px;
  width: 650px;
  padding: 10px;
  background-color: rgb(3, 3, 20);
  color: white;
  
}
.output-area iframe{
  height: 1500px;
  width: 850px; 
  padding: 10px;
  background-color: whitesmoke;
  color: black;
  
}
label{
  margin: 20px;
  font-weight: bold;


}
label i{
  margin-right: 10px;
}